<template>
    <el-dialog title="本地仓库详情" align-center width="640px">
        <el-form label-width="128px">
            <el-form-item label="名称"> {{ item.name }} </el-form-item>
            <el-form-item label="标题"> {{ item.label }} </el-form-item>
            <el-form-item label="标签"> {{ item.tags }} </el-form-item>

            <el-form-item label="ID / UUID"> {{ item.id }} / {{ item.uuid }} </el-form-item>
            <el-form-item label="分组"> {{ item.group }} </el-form-item>

            <el-form-item label="有工作区"> {{ !item.bare }} </el-form-item>
            <el-form-item label="路径"> {{ item.repository_path }} </el-form-item>
            <el-form-item label="状态"> {{ item.state }} </el-form-item>

            <el-form-item label="描述"> {{ item.description }} </el-form-item>

            <el-form-item label="创建时间"> {{ formatTime(item.created_at) }} </el-form-item>
            <el-form-item label="修改时间"> {{ formatTime(item.updated_at) }} </el-form-item>
        </el-form>

        <div>
            <ElButton type="default" @click="close">关闭</ElButton>
        </div>

    </el-dialog>
</template>

<script>
import { ElButton } from 'element-plus';
import TimeJS from '@/js/time.js'

export default {
    name: "settings-local-repository-item-detail-dialog",

    components: { ElButton },

    computed: {
        item() {
            return this.target;
        }
    },

    methods: {
        close() {
            this.$emit('update:modelValue', false)
        },

        formatTime(ms) {
            return TimeJS.StringifyTimestamp(ms, 'datetime')
        },
    },

    props: {
        target: Object
    }
}
</script>
